.boxRegister {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(109,27,50);
  background: linear-gradient(153deg, rgba(109,27,50,1) 0%, rgba(25,5,30,1) 45%, rgba(5,1,24,1) 100%);
}

.card {
  width: 450px;
  border: 1px solid #919191;
  border-radius: 4px;
  box-shadow: 2px 2px 8px rgba(0,0,0,0.1);
  overflow: hidden;
  margin: 10px;
  background-color: #fff;
}

.cardHeader {
  background-color: #333;
  padding: 16px;
  text-align: center;
}

.cardHeader .textHeader {
  margin: 0;
  font-size: 18px;
  color: rgb(255, 255, 255);
}

.cardBody {
  padding: 16px;
}

.formGroup {
  margin-bottom: 10px;
}

.formGroup label {
  display: block;
  font-size: 14px;
  color: #333;
  font-weight: bold;
  margin-bottom: 1px;
}

.formGroup input[type="text"],
.formGroup input[type="email"],
.formGroup input[type="password"] {
  width: 90%;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.btn {
  padding: 12px 24px;
  margin-left: 13px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  background-color: #333;
  color: #fff;
  text-transform: uppercase;
  transition: background-color 0.2s ease-in-out;
  margin: 10px;
  cursor: pointer
}

.btn:hover {
  background-color: #ccc;
  color: #333;
}

.error {
  color: red;
  font-size: small;
}

.link {
  color: #1778f2;
  text-decoration: none;
}

.boxLink {
  align-self: center;
  font-weight: 500;
  text-align: center;
}

.boxLink .link {
  font-weight: 400;
}

.link:hover {
  text-decoration: underline;
}

.boxError {
  margin: 20px auto;
  display: flex;
  padding: 25px;
  justify-content: center;
  align-items: center;
  height: 80px;
  color: rgb(36, 27, 0);
  font-weight: bold;
  background-color: #aa941485;
  border: 2px solid #ffd417;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}